.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  
    .logo-container {
      height: 100%;
      width: 70px;
      padding: 25px;
    }
  
    .options {
      display: flex;
      height: 100%;
      width: 50%;
      justify-content: flex-end;
      align-items: center;
      
      .option {
        padding: 10px 15px;
        cursor: pointer;
      }
  
      .cart-icon {
        width: 45px;
        height: 45px;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;  
  
        .shopping-icon {
          width: 24px;
          height: 24px;
        }
      }
  
      .cart-dropdown {
        position: absolute;
        width: 240px;
        height: 340px;
        display: none;
        background-color: white;
        border: 1px solid black;
        top: 90px;
        right: 40px;
        padding: 20px;
        z-index: 5;
  
        &.show {
          display: block;
        }
        
        .cart-items {
          height: 240px;
          display: flex;
          flex-direction: column;
          overflow: scroll;
        }
  
        .cart-item {
          width: 100%;
          display: flex;
          height: 80px;
          margin-bottom: 10px;
  
          img {
            width: 30%;
          }
  
          .item-details {
            width: 70%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            padding: 10px 20px;
  
            .name {
              font-size: 16px;
              margin-bottom: 10px;
            }
  
            .price {
              font-size: 18px;
            }
          }
        }
  
        .custom-button {
          width: 100%;
          height: 40px;
          background-color: #000;
          color: white;
          padding: 20px auto;
          margin: 20px auto;
  
          &:hover {
            background-color: lightgrey;
            color: black;
          }
        }
  
  
      }
    }
  }